* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(rgb(138, 43, 226, 0.3), rgb(138, 43, 226, 0.3)), url(./images/dice.avif);
  opacity: 0.9;
}

main {
  max-width: 800px;
  height: 450px;
  background-color: rgb(44, 192, 241);
  border-radius: 10px;
  margin: 2rem auto 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.container {
  display: grid;
  grid-template: auto auto/ repeat(5, 1fr);
  gap: 1rem;
}

.box {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  background-color: white;
  box-shadow: 0px 1px 5px rgba(0,0,0,0.8);
  cursor: pointer;
}

button {
  padding: 10px 30px;
  font-size: 1.5rem;
  margin-top: 30px;
  background-color: darkviolet;
  color: white;
  border: none;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}

button:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.instructions {
  margin: 1.5rem;
  text-align: center;
  width: 60%;
}

.text {
  display: flex;
  gap: 2rem;
}

.roll, .score {
  margin: 1rem;
}